$reference-toc-width: 226px;
$reference-body-left: 227px;
$reference-padding: 20px;

/* Navbar */
.reference-nav {
	padding: 12px 20px;
	border-bottom: 1px solid $border;
}

/* Article */
.reference-content {
  @mixin content;

  /* ~16px */
  font-size: 1.142857em;
  padding-left: $reference-padding;
  padding-right: $reference-padding;

  & h6 {
		font-size: 0.7em;
    margin-top: 20px;
  }

  & .alert {
    background: $white;
    border: 1px solid color($border alpha(50%));
    padding: 12px;

    & li {
      margin-top: 0;
    }

    & h5 {
      color: $text-strong;
      font-size: 0.7em;
      font-weight: 400;
      letter-spacing: 0.65px;
      margin: 0.35em 0;
      text-transform: uppercase;
    }

    & p {
      margin: 0;
    }
  }
}

/* Use position model to layout the toc sidebar and body area */
.reference {
  box-sizing: border-box;
  position: relative;
  overflow: auto;

  & .reference-toc {
    background: $background;
    float: left;
    width: $reference-toc-width;
    overflow-y: auto;
  }

  .reference-body-outer {
    margin-left: $reference-body-left;
    position: relative;
  }

  & .reference-body {
    float: left;
    width: 100%;
    overflow-y: hidden;
  }
}

/* ------ */
/* TODO: Will deprecate when we start to touch the rendered content area */
.reference-body {
	& .header,
	& .level {
	  @mixin grids;
		margin-bottom: 0;

	  & > .primary,
	  & > .secondary {
	    flex: 1;
	    /*flex: none;*/
	    padding: 0 10px;
	    max-width: 50%;
	    width: 50%;
	  }

		& > .primary {
			margin-right: 10px;
		}
		& > .secondary {
			margin-left: 10px;
		}
	}
}

.list-clean {
  list-style-type: none;
  padding: 0;
}

.expand-container {
	/* Class name*/
	& .expander {
		position: relative;
		padding-right: 14px;

		& code {
			background: transparent;
			border: none;
		}

		&:after {
			@mixin arrow-down $link;
			margin-top: -3px;
			right: 0;
			top: 50%;
		}
		&:hover {
			&:after {
				border-color: $link-hover;
			}
		}

		&.expanded {
      &:after {
        @mixin arrow-up $link;
        margin-top: 3px;
      }
      &:hover {
        &:after {
          border-color: $link-hover;
        }
      }
    }
	}

	/* Expandable content */
	& .content {
		background: $white;
		border-top: 1px solid $background;
		border-bottom: @border-top;
		margin-left: -20px;
		margin-right: @margin-left;
		padding: 16px 20px 32px;
	}
}
